import React, { useEffect, useRef, useState, useContext } from 'react';
import { DataContext } from '../../DataContext';
import { validateImageType, handleImage } from '../../logic/helperMethods';
import { createPost, postPicsUploadFailed } from '../../logic/api';
import './CreatePostCard.css';
import Svgs from '../../Assets/icons/Svgs';
import CryptoJS from 'crypto-js';

const CreatePostCard = ({ username }) => {

    const [postImages, setPostImages] = useState([]);
    const [title, setTitle] = useState("");
    const [error, setError] = useState("");
    const chooseImagesRef = useRef();
    const { userUsername, setUserUsername, profileImageName } = useContext(DataContext);

    const publishPost = async() => {
        try{

            let extName = profileImageName ? profileImageName.split('.')[1] : "error";

            if(extName !== "jpg" && extName !== "png" || extName === "error") {
                const askUser = window.confirm("There is error in your profile photo format\ncreate post without your photo ?");
                if(askUser === false)
                    return setError("Error in your profile picture");
            }

            if(!postImages || postImages.length <= 0) return setError("no images selected :(");

            let postPictures = postImages;
            for (let i = 0; i < postPictures.length; i++) {
                if(!validateImageType(postPictures[i])) return setError(`Picture No.${i} is not a valid picture`);
                if(postPictures[i].size <= 0) return setError("No Picture found");
                if(postPictures[i].size > 550000){
                    postPictures[i] = await handleImage(postPictures[i], 0.50);
                    if(postPictures[i].size > 550000) return setError(`Error optimising your image number: ${i + 1} , please reduce it size`);
                }
            };

            if(title && title.length > 350) return setError("Title is too long");

            if(username !== userUsername) setUserUsername(username);

            const res = await createPost(username, profileImageName, postPictures, title);

            if(!res || !res?.ok || res.ok === false) return setError(res?.dt ? res.dt : "Error creating post");

            if(!res.dt.picturesNames || res.dt.picturesNames.length < postPictures.length 
                || res.dt.picturesNames.length > postPictures.length) postPicsUploadFailed(res.dt.postId);
   
            console.log("Create Post response before uploading pictures: ", res.dt);

            console.log("pic no.1: ", postPictures[0]);

            const reader = new FileReader();
            reader.onload = function() {
                const hash = CryptoJS.SHA1(CryptoJS.enc.Latin1.parse(reader.result));
                const xhr = new XMLHttpRequest();
                xhr.withCredentials = false;
                xhr.addEventListener("load", function () {
                let msg;
                if (xhr.status >= 200 && xhr.status < 300) {
                    msg = '2xx response from B2 API. Success.';
                } else if (xhr.status >= 400 && xhr.status < 500) {
                    msg = '4xx error from B2 API. See other console log messages and requests in network tab for details.';
                    postPicsUploadFailed(res.dt.postId);
                } else if (xhr.status >= 500) {
                    msg = '5xx error from B2 API. See other console log messages and requests in network tab for details.';
                    postPicsUploadFailed(res.dt.postId);
                } else {
                    msg = 'Unknown error. See other console log messages and requests in network tab for details.';
                    postPicsUploadFailed(res.dt.postId);
                }
                console.log(`Upload file result: ${msg}`);
                });
                xhr.open("POST", res.dt.uploadUrl);
                xhr.setRequestHeader("Content-Type", postPictures[0].type);
                xhr.setRequestHeader("Authorization", res.dt.authToken);
                xhr.setRequestHeader("X-Bz-File-Name", res.dt.picturesNames[0]);
                xhr.setRequestHeader("X-Bz-Content-Sha1", hash);
                const fileToSend = postPictures[0];
                xhr.send(fileToSend);
            };
            reader.readAsBinaryString(postPictures[0]);
            

            setPostImages([]);
            setTitle("")
            setError("");

        } catch(err){
            setError(err.message);
        }
    };

    const generateURL = (image) => {
        if(image){
            const url = URL.createObjectURL(image);
            if(url)
                return url;
        }
    };

    return (
        <div className='CreatePostCard'>

            <div className='CreatePostHeader'>       

                <h2>Add your images</h2>

                <button onClick={() => {
                    chooseImagesRef.current.click();
                }}><Svgs type={"ImageIcon"}/>Browse </button>

                <button className='publishButton' onClick={() => {
                    publishPost();
                }}
                style={{
                    background: postImages.length > 0 ? null : "grey",
                    borderColor: postImages.length > 0 ? null : "transparent"
                }}
                ><Svgs type={"Upload"}/></button>

            </div>
            

            <div className='postImagesDiv'>
                {postImages.length > 0 ? (<ul>
                    {postImages.map((image) => (
                        <li>
                            <img src={generateURL(image)} alt='' 
                            onClick={() => {
                                setPostImages(
                                    postImages.filter(
                                        i => i !== image
                                    )
                                );
                                setError(null);
                            }}/>
                        </li>
                    ))}
                </ul>) : (
                    <div className='NoImageHolder'>
                        <Svgs type={"404_icon"}/>Browse images
                     </div>   
                )}
            </div>

            <label style={{color: "red", paddingLeft: 16}}>{error}</label>

            <div className='inputDesc'>
                <label>Title</label>
                <textarea rows={2} maxLength={100} onChange={(e) => {
                    setTitle(e.target.value);
                }}/>
            </div>

            <input 
                style={{display: 'none'}}
                ref={chooseImagesRef} 
                type='file' 
                onChange={(e) => {
                    if(postImages.length > 2){
                        setError("Only 3 images are allowed in one post");
                        setTimeout(() => {
                            setError(null);
                        }, 5000);
                    } else {    
                        const files = e.target.files;
                        if(files && postImages && files[0])
                            setPostImages([...postImages, files[0]]);
                    }
                }}/>

        </div>
    )
};

export default CreatePostCard;
